* {
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: Avenir;
  src: url("./fonts/Avenir-Book.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("./fonts/Avenir-Heavy.ttf");
  font-weight: 600;
}

body {
  font-family: Avenir, sans-serif;
  font-size: 16px;
  margin: 8px;
}

.search {
  display: flex;
  justify-content: center;
  margin: 20px auto 0;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  color: #3d4e61;
  box-shadow: 0 1px 8px rgb(61 78 97 / 10%);
  outline: none;
  font-family: inherit;
  font-weight: 300;
  box-sizing: border-box;
}

.search-big {
  width: 100%;
  height: 70px;
  padding-left: 62px;
  line-height: 22px;
  font-size: 16px;
}

.search-small {
  width: 100%;
  height: 42px;
  padding-left: 33px;
  line-height: 19px;
  font-size: 14px;
}

.label-search {
  position: relative;
  display: block;
  width: 100%;
}

.label-search-big:after {
  content: "";
  display: block;
  background-image: url(./images/Search.svg);
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.label-search-small:after {
  content: "";
  display: block;
  background-image: url(./images/Search.svg);
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.search ::placeholder {
  color: inherit;
  font-weight: 300;
}

.search:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 600;
}

.search:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
  border-radius: 4px;
}

.search-big::placeholder {
  font-size: 16px;
  line-height: 22px;
}

.search-small::placeholder {
  font-size: 14px;
  line-height: 22px;
}
